        openInteractiveMeshDialog(gltfURL, webpURL) {
            this.interactiveMeshDialog.currentGltfURL = gltfURL;
            this.interactiveMeshDialog.currentWebpURL = webpURL;
            this.interactiveMeshDialog.showDialog = true;
        },
        resetTooltipText() {
            this.tooltipText = "Click to Copy Scan ID"
            this.tooltipColor = undefined
        },
        getColor(label) {
            if (label === "whole") return '#dde439'
            else if (label === "partial") return '#f15095'
            else return '#6c604a'
        },
        copyID(sampleID) {
            let that = this
            this.$copyText(sampleID).then(
                function () {
                    that.tooltipText = "Copied"
                    that.tooltipColor = "success"
                },
                function () {
                    that.tooltipText = "Error, your browser environment does not support this operation"
                    that.tooltipColor = "error"
                },
            )
        },
        pauseCurrentVideo(e) {
            let video = e.currentTarget;
            video.pause()
        },
        playCurrentVideo(e) {
            if (!this.playBtn) {
                return
            }
            let video = e.currentTarget;
            video.play()
        },
