            let temp = this.$refs.rel_obj.$el.clientWidth * 0.15
            this.imgWidth = temp + 'px'
            this.imgHeight = temp * 0.8 + 'px'
        },

        loadDatasetFile() {
            this.tableData = []
            this.playBtn = true
            this.playbackRate = 0.5
            let scanIds = ["scene_00024_00", "scene_00024_01"]

            for (let key of scanIds) {
                let objFilePrefix = "https://aspis.cmpt.sfu.ca/projects/multiscan/multiscan_examples";

                let key_id = key
                this.tableData.push({
                    id: key_id,
                    rm_type: "xxxxx",
                    texturedMeshThumbnailUrl: objFilePrefix + "/" + key_id + "/" + key_id + "-texture.webp",
                    semThumbnailUrl: objFilePrefix + "/" + key_id + "/" + key_id + "-instance.webp",
                    obbThumbnailUrl: objFilePrefix + "/" + key_id + "/" + key_id + "-obb.webp",
                    textured_mesh: objFilePrefix + "/" + key_id + "/" + key_id + "-texture.glb",
                    sem_part_inst: objFilePrefix + "/" + key_id + "/" + key_id + "-instance.glb",
                    sem_obb: objFilePrefix + "/" + key_id + "/" + key_id + "-obb.glb",
                    articuMesh: objFilePrefix + "/" + key_id + "/" + key_id + "-articulation.glb",
                    articuWebpUrl: objFilePrefix + "/" + key_id + "/" + key_id + "-articulation.webp",
                    articuAnimWebpUrl1: objFilePrefix + "/" + key_id + "/" + key_id + "-animation-camera0.webp",
                    articuAnimWebpUrl2: objFilePrefix + "/" + key_id + "/" + key_id + "-animation-camera1.webp",
                    articuAnimWebpUrl3: objFilePrefix + "/" + key_id + "/" + key_id + "-animation-camera2.webp",
                    articuAnimWebpUrl4: objFilePrefix + "/" + key_id + "/" + key_id + "-animation-camera3.webp",
                })
            }

